export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			//let list = await this.$api.json('cateList');
			var _this = this;
			uni.request({
			    url: this.apiServer + "/api/category/list",
			    dataType: "JSON",
			    success: function(res) {
			      let list = res.data;
					list.forEach(item=>{
						if(!item.level){
							_this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
						}else if(!item.description){
							_this.slist.push(item); //没有图的是2级分类
						}else{
							_this.tlist.push(item); //3级分类
						}
					})
			    }
			});

		},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}				
			this.currentId = item.id;
			let index = this.slist.findIndex(sitem=>sitem.level === item.id);
			if(index != -1){
				this.tabScrollTop = this.slist[index].top;
			}

		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			console.log(tabs)
			if(tabs.length > 0){
				this.currentId = tabs[0].level;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(sid, tid){
			uni.navigateTo({
				url: `/pages/product/list?fid=${this.currentId}&sid=${sid}&tid=${tid}`
			})
		}
	}
}

